import React, { useState} from "react"
import Button from "../components/Button"
import fetch from 'cross-fetch';
import InputKey from '../components/InputKey'

export default function Message() {
    const [message, setMessage] = useState({});
    const [key, setKey] = useState('');
    const [isKey, setIsKey] = useState(false);

    const apiLink = "https://out-of-context-neira.herokuapp.com/?key=" + key

  

  const fetchData = () => {
    fetch(apiLink)
    .then(res => res.json())
    .then((data) => {setMessage(data)})
    .catch(setIsKey(false))
  }

  const reloadMessage = () => {
    fetchData()
  }

  const keyAction = (e) =>
  {
    setKey(e.target.value)
  }

  const sendKey = () => {
    fetchData()
    setIsKey(true)
  }

  // if (!message.text &) fetchData();
    return (
        <div className="message">
            <h2>{message.text}</h2>
            {!isKey && <InputKey action={keyAction} send={sendKey}/>}
            {key && <>
              <b className="sender">{message.sender}</b>
              <p className="info"> {message.date}
              <br></br>
              {message.time}
              </p>
            <Button name='One More!' action={reloadMessage}/></>}
            
        </div>
      )
  
  }